import { Component, OnInit } from '@angular/core';
import { Slide } from './shared/slides/slide.interface';
import { AnimationType } from './shared/carousel/carousel.animations';
import { Barcelona92 } from 'src/app/shared/slides/barcelona92.slides';
import { PlayerService } from 'src/app/shared/services/player.service'
import { DataService } from './shared/services/data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements OnInit {
  title = 'dublab bcn';
  canShowTooltip = false;
  animationType = AnimationType.Fade;
  slides: Slide[] = new Barcelona92().slides;

  mouseOnScheduleDropDown: boolean = false;
  mouseOnScheduleMenu: boolean = false;
  mouseOnScheduleMenuToBeFalse: boolean = false;

  // Explore
  exploreData = [
    { display: 'Shows', link: ''},
    { display: 'B-Sides', link: ''},
    { display: 'Destacats', link: ''},
    { display: 'Ex-Shows', link: ''},
  ];
  mouseOnExploreDropDown: boolean = false;
  mouseOnExploreMenu: boolean = false;
  mouseOnExploreMenuToBeFalse: boolean = false;
  // Sisters
  sistersLinks = [
    { display: 'dublab.com', link: 'dublab.com'},
    { display: 'dublab.de', link: 'dublab.de'},
    { display: 'dublab.jp', link: 'dublab.jp'},
    { display: 'dublab.br', link: 'dublab.com.br'},
  ];
  mouseOnSistersDropDown: boolean = false;
  mouseOnSistersMenu: boolean = false;
  mouseOnSistersMenuToBeFalse: boolean = false;

  constructor(private playerService: PlayerService,
    private dataService: DataService) { }

  ngOnInit(): void {
    this.initializeTooltip();
    this.dataService.createTodaysSchedule();
  }

  public get todaysSchedule() {
    return this.dataService.todaysSchedule;
  }

  public get hasTunedIn(): boolean {
    return this.playerService.hasTunedIn;
  }

  // Events
  public onTuneIn() {
    this.playerService.tuneIn();
  }

  // Dropdown Managers
  // Schedule
  public onShowSchedule(): void {
    this.mouseOnScheduleMenu = true;
    this.mouseOnScheduleMenuToBeFalse = false;

    this.mouseOnExploreMenu = false;
    this.mouseOnSistersMenu = false;
  }

  public onHideSchedule(): void {
    this.mouseOnScheduleMenuToBeFalse = true;

    this.delay(500).then(any => {
      if (this.mouseOnScheduleMenuToBeFalse) {
        this.mouseOnScheduleMenu = false;
      }
    });
  }

  public onShowScheduleDropDown(): void {
    this.mouseOnScheduleDropDown = true;
  }

  public onHideScheduleDropDown(): void {
    this.mouseOnScheduleDropDown = false;
  }

  // Explore
  public onShowExplore(): void {
    this.mouseOnExploreMenu = true;
    this.mouseOnExploreMenuToBeFalse = false;

    this.mouseOnScheduleMenu = false;
    this.mouseOnSistersMenu = false;
  }

  public onHideExplore(): void {
    this.mouseOnExploreMenuToBeFalse = true;

    this.delay(500).then(any => {
      if (this.mouseOnExploreMenuToBeFalse) {
        this.mouseOnExploreMenu = false;
      }
    });
  }

  public onShowExploreDropDown(): void {
    this.mouseOnExploreDropDown = true;
  }

  public onHideExploreDropDown(): void {
    this.mouseOnExploreDropDown = false;
  }

  // Sisters
  public onShowSisters(): void {
    this.mouseOnSistersMenu = true;
    this.mouseOnSistersMenuToBeFalse = false;

    this.mouseOnScheduleMenu = false;
    this.mouseOnExploreMenu = false;
  }

  public onHideSisters(): void {
    this.mouseOnSistersMenuToBeFalse = true;

    this.delay(500).then(any => {
      if (this.mouseOnSistersMenuToBeFalse) {
        this.mouseOnSistersMenu = false;
      }
    });
  }

  public onShowSistersDropDown(): void {
    this.mouseOnSistersDropDown = true;
  }

  public onHideSistersDropDown(): void {
    this.mouseOnSistersDropDown = false;
  }

  // Tooltip

  public initializeTooltip(): void {
    const tooltip = document.getElementsByClassName("tooltip") as HTMLCollectionOf<HTMLElement>;
    document.addEventListener('mousemove', fn, false);

    function fn(e) {
      for (let i = tooltip.length; i--;) {
          tooltip[i].style.left = e.pageX + 'px';
          tooltip[i].style.top = e.pageY + 'px';
      }
    }
  }

  public showTooltip(): void {
    this.canShowTooltip = true;
  }

  public hideTooltip(): void {
    this.canShowTooltip = false;
  }

  private async delay(ms: number) {
    await new Promise(resolve => setTimeout(() => resolve(), ms));
  }
}
