import { Component, Input, OnInit, OnDestroy } from "@angular/core";
import { Slide } from "../slides/slide.interface"
import { trigger, transition, useAnimation } from "@angular/animations";
import {
  AnimationType,
  scaleIn,
  scaleOut,
  fadeIn,
  fadeOut,
  flipIn,
  flipOut,
  jackIn,
  jackOut
} from "./carousel.animations";
import { interval } from 'rxjs';

@Component({
  selector: 'carousel',
  templateUrl: './carousel.component.html',
  styleUrls: ['./carousel.component.css'],
  animations: [
    trigger("slideAnimation", [
      /* scale */
      transition("void => scale", [
        useAnimation(scaleIn, { params: { time: "500ms" } })
      ]),
      transition("scale => void", [
        useAnimation(scaleOut, { params: { time: "500ms" } })
      ]),

      /* fade */
      transition("void => fade", [
        useAnimation(fadeIn, { params: { time: "2500ms" } })
      ]),
      transition("fade => void", [
        useAnimation(fadeOut, { params: { time: "2500ms" } })
      ]),

      /* flip */
      transition("void => flip", [
        useAnimation(flipIn, { params: { time: "500ms" } })
      ]),
      transition("flip => void", [
        useAnimation(flipOut, { params: { time: "500ms" } })
      ]),

      /* JackInTheBox */
      transition("void => jackInTheBox", [
        useAnimation(jackIn, { params: { time: "700ms" } })
      ]),
      transition("jackInTheBox => void", [
        useAnimation(jackOut, { params: { time: "700ms" } })
      ])
    ])
  ]
})
export class CarouselComponent implements OnInit {
  @Input() slides: Slide[];
  @Input() controls: boolean = false;
  @Input() random: boolean = false;
  @Input() autoTime: number = 0;
  @Input() animationType = AnimationType.Fade;

  currentSlide = 0;
  cachedNextImageId: number;

  constructor() {}

  ngOnInit(): void {
    if (this.random) this.randomClick();

    if (this.autoTime > 0) {
      interval(this.autoTime).subscribe(x => {
        if (this.random) this.randomClick();
        else this.onNextClick();
      });
    }
  }


  public onPreviousClick() {
    const previous = this.currentSlide - 1;
    this.currentSlide = previous < 0 ? this.slides.length - 1 : previous;
  }

  public onNextClick(skip: number = 1) {
    if  (!this.cachedNextImageId) {
      this.cachedNextImageId = this.currentSlide + skip;
    }

    this.currentSlide = this.cachedNextImageId === this.slides.length ? 0 : this.cachedNextImageId;
    this.cachedNextImageId = this.currentSlide + skip;
    this.preloadImage(this.cachedNextImageId);
  }

  public randomClick() {
    if (!this.cachedNextImageId) {
      this.cachedNextImageId = this.getRandomInt(1, this.slides.length);
    }

    this.currentSlide = this.cachedNextImageId === this.slides.length ? 0 : this.cachedNextImageId;
    this.cachedNextImageId = this.getRandomInt(0, this.slides.length - 1);
    this.preloadImage(this.cachedNextImageId);
  }

  public onRightClick(event): void {
    event.preventDefault();
  }

  private preloadImage(id) {
    let slide = this.slides[id];

    if (!slide) {
      slide = this.slides[0];
      this.cachedNextImageId = 0;
    }

    if (slide.src) {
      new Image().src = slide.src;
    }
  }

  private getRandomInt(min: number, max: number) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }
}
