import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { DataService } from 'src/app/shared/services/data.service'

@Component({
  selector: 'menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css'],
})
export class MenuComponent implements OnInit {
  @Output() showSchedule = new EventEmitter<any>();
  @Output() hideSchedule = new EventEmitter<any>();

  @Output() showExplore = new EventEmitter<any>();
  @Output() hideExplore = new EventEmitter<any>();

  @Output() showSisters = new EventEmitter<any>();
  @Output() hideSisters = new EventEmitter<any>();

  constructor() {}

  ngOnInit(): void {}

  // Schedule
  onShowSchedule(): void {
    this.showSchedule.emit();
  }

  onHideSchedule(): void {
    this.hideSchedule.emit();
  }

  // Explore
  onShowExplore(): void {
    this.showExplore.emit();
  }

  onHideExplore(): void {
    this.hideExplore.emit();
  }

  // Sisters
  onShowSisters(): void {
    this.showSisters.emit();
  }

  onHideSisters(): void {
    this.hideSisters.emit();
  }
}
