import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { TimeService } from 'src/app/shared/services/time.service';
import { MenuItem } from 'src/app/shared/interfaces/menu-item.interface'
import { Subscription } from 'rxjs';
import { DataService } from '../services/data.service';

@Component({
  selector: 'drop-down',
  templateUrl: './drop-down.component.html',
  styleUrls: ['./drop-down.component.css'],
})
export class DropDownComponent implements OnInit, OnDestroy {
  currentHour: number;
  todaysDate: Date;

  @Input() menuData: MenuItem[];
  @Input() links: any;

  @Output() mouseEnter = new EventEmitter<any>();
  @Output() mouseLeave = new EventEmitter<any>();

  currentHourSubscription: Subscription;
  todaysDateSubscription: Subscription;

  constructor(private timeService: TimeService, private dataService: DataService) { }

  ngOnInit(): void {
    this.currentHourSubscription = this.timeService.currentHour$.subscribe(hour => {
      this.currentHour = hour;
    })
    this.todaysDateSubscription = this.timeService.airtimeTime$.subscribe(date => {
      this.todaysDate = date;
    })
  }

  ngOnDestroy() {
    this.currentHourSubscription.unsubscribe();
    this.todaysDateSubscription.unsubscribe();
  }

  public get isScheduleMenu(): boolean {
    return this.menuData &&
      this.menuData[0] != undefined &&
      this.menuData[0].hour != undefined;
  }

  public get currentShow() {
    return this.dataService.currentShow;
  }

  public get currentDate() {
    return this.timeService.airtimeTime$;
  }

  public showHasAlreadyTakenPlace(next: any): boolean {
    return this.dataService.showHasAlreadyTakenPlace(next);
  }

  onMouseEnter(): void {
    this.mouseEnter.emit();
  }

  onMouseLeave(): void {
    this.mouseLeave.emit();
  }
}
