import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PlayerService {
  hasTunedIn: boolean = false;
  playing: boolean = false;

  dublab: any = new Audio(environment.streamUrl);

  constructor() { }

  public toggleTuneIn() {
    if (!this.hasTunedIn) this.hasTunedIn = true;

    if (this.playing) {
      this.stop();
      this.playing = false
    } else {
      this.play();
      this.playing = true;
    }
  }

  public tuneIn() {
    if (!this.hasTunedIn) this.toggleTuneIn();
  }

  public play() {
    if (this.dublab.muted) {
      this.dublab.muted = false;
    } else {
      this.dublab.play();
    }
  }

  public stop() {
    this.dublab.muted = true;
  }
}
